:root
{
    --blue: #287bff;
    --blue2: #001936;
    --white: #fff;
    --grey: #f5f5f5;
    --black1: #222;
    --black2: #999;
    --red: #F52A18;
    --orange: #F59D00;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: 0;
    outline: 0;
    font-family: 'Montserrat', sans-serif;
    text-decoration: none;
    list-style: none;
}

body{
    background: rgb(2,0,36);
    background: linear-gradient(193deg, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 35%, rgba(0,121,255,1) 100%); 
}

.aaa{
    background-color: red;
}

/*ESTILOS NAVBAR*/
nav{
    position: fixed;
    z-index: 10;
    left: 0;
    right: 0;
    top: 0;
    padding: 8 5%;
    height: 100px;
    background-color: var(--blue2) ;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

nav .logo{
    float: left;
    width: 40%;
    height: 100%;
    display: flex;
    align-items: center;
    font-size: 24px;
    margin-left: 1vw;
    opacity: 1;
}

nav .logo a{
    color: var(--white);
    text-decoration: none;
} 

nav .links{
    float: right;
    padding: 0;
    margin: 0;
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

nav .links .items{
    display: block;
    padding: 1em;
    font-size: 16px;
    font-weight: bold;
    color: var(--white);
    text-decoration: none;
}

#nav-toggle{
    position: absolute;
    top: -180px;
}

nav .iconNav{
    display: none;
    position: absolute;
    right: 5%;
    top: 50%;
    transform: translateY(-58%);
}

nav .iconNav .line{
    width: 30px;
    height: 5px;
    background-color: var(--white);
    margin: 5px;
    border-radius: 3px;
    transition: all .3s ease-in-out;
}
/*FIN ESTILOS NAVBAR*/

/*HOME PAGE*/

.principal{
    margin: 0 auto;
    margin-top: 8%;
    width: 90%;
}

.principal img{
    float: left;
    height: 70%;
    margin: 0;
}

.principal h1{
    font-size: 65px;
    font-weight: bold;
    margin-top: 5%;

    color: var(--white);
}

.principal h1 strong{
    color: var(--orange);
}

.principal p{
    color: var(--white);
    font-size: 15px;
    width: 60%;
}

.principal a{
    display: block;
    margin-top: 0;
    width: 80%;
    background-color: var(--orange);
    color: var(--blue2);
    text-decoration: none;
    text-align: center;
    border-radius: 5px;
    font-size: 30px;
    font-weight: bold;
}

.why{
    background-color: var(--blue2);
    margin: 0 auto;
    margin-top: 12%;
    width: 80%;
    border-radius: 20px;
    border: 1px solid rgba(0,0,0,0.08) ;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);   
}

.why h2{
    color: var(--white);
    text-align: center;
    padding: 20px;
    font-size: 40px;
    font-weight: bold;
}

.why p{
    color: var(--white);
    text-align: justify;
    width: 80%;
    margin: 0 auto;
}

.servicios{
    width: 18rem;
    background-color: var(--blue2);
    border-radius: 5px;
}

.servicios img{
    width: 100%;
    height: 300px;
}

.servicios h5{
    color: var(--orange);
}

.servicios p{
    color: var(--white);
}

/*FIN ESTILOS HOME PAGE*/

/*FORMULARIOS*/

.formulario{
    margin: 0 auto;
    margin-top: 12%;
    border-radius: 20px;
    width: 50%;
    background-color: rgba(0,0,0,0.8);
    color: var(--white);
    border: 1px solid rgba(0,0,0,0.08) ;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);   
}

.form{
    margin: 0 auto;
    height: 50vh;
}

.formulario h3{
    text-align: center;
    margin-top: 15px;
}

.login{
    width: 30%;
}

.formLogin{
    margin: 0 auto;
    height: 40vh;
    width: 100%;
}

.btnLogin button,
.btnRegistrar button{
    margin: 0 auto;
    width: 100%;
    background-color: var(--blue);
}
/*FIN FORMULARIOS*/


/*FOOTER*/
.footer{
    margin-top: 5%;
    height: 50px;
    background-color: #F59D00;
}

.footer p{
    color: var(--black1);
    font-size: 20px;
    text-align: center;
    justify-content: space-around;
}

/*FIN ESTILOS FOOTER*/

/*RESPONSIVE*/
@media screen and (max-width: 998px) {
    /*HOME PAGE*/

    .principal{
        margin-top: 10%;
    }

    .principal img{
        float: right;
        height: 50%;
    }

    .principal h1{
        font-size: 45px;
    }


    .principal p{
        font-size: 20px;
        width: 100%;
    }

    .principal a{
        margin: 0 auto;
    }

    /*FIN ESTILOS HOME PAGE*/ 
}

@media screen and (max-width: 768px) {

    /*ESTILOS NAVBAR*/
    nav .logo{
        float: none;
        width: auto;
        justify-content: center;
    }

    nav .links{
        float: none;
        position: fixed;
        z-index: 9;
        left: 0;
        right: 0;
        top: 100px;
        bottom: 100%;
        width: auto;
        height: auto;
        flex-direction: column;
        justify-content: space-evenly;
        background-color: rgba(0, 0, 0, .8);
        overflow: hidden;
        transition: all .5s ease-in-out;
     }

     nav .links .items{
        font-size: 20px;
    }
    
    nav :checked ~ .links{
        bottom: 0;
    }

    nav .iconNav{
        display: block;
    }
    
    nav :checked ~ .iconNav .line:nth-child(1){
        transform: translateY(10px) rotate(225deg);
    }

    nav :checked ~ .iconNav .line:nth-child(3){
        transform: translateY(-10px) rotate(-225deg);
    }

    nav :checked ~ .iconNav .line:nth-child(2){
        opacity: 0;
    }

    /*FIN ESTILOS NAVBAR*/

    /*HOME PAGE*/

    .principal{
        margin-top: 15%;
    }

    .principal img{
        float: none;
        margin: 0 auto;
        display: block;
        margin-top: 22px;
    }

    .principal h1{
        font-size: 65px;
    }

    .principal a{
        margin: 0 auto;
    }


    .groupServices{
        display: block;
    }

    .servicios{
        width: 100%;
        margin: 0 auto;
    }

    .servicios img{
        width: 100%;
        border-radius: 5px;
    }

    /*FIN ESTILOS HOME PAGE*/   
}

@media screen and (max-width: 480px) {
    /*HOME PAGE*/

    .principal{
        margin-top: 20%;
    }

    .principal p{
        font-size: 20px;
        width: 100%;
    }

    .why p{
        font-size: 20px;
    }


    
    /*FIN ESTILOS HOME PAGE*/   
}